.header {
    font-family: 'Noto Sans KR';
    font-weight: 600;
    color: #333;
}
.header a:link {
    color: whitesmoke;
    text-decoration: none; 
}

.header a:visited {
    color: inherit;
    text-decoration: none !important;
}
.header a:hover {
    color: #00e6cb;
}

.header {
    background-color: #272b38;
    font-size: 18px;
    color: whitesmoke;
    display: flex;
    justify-content: space-between;
    height: 50px;
    border-bottom: 1px solid black;
    /*min-width: 600px;*/
}
.header__elem1 {
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header__elem1__logo {
    display: flex;
}
.header__elem1__logo:first-child {
