}

.wrapper {
    display: flex;
    flex-direction: column;
    padding: 10px;
    height: 100%;
}
.row {
    display: flex;
    /*background-color: whitesmoke;*/
    background-color: #272b38;
    font-family: 'Kopub M';
    font-weight: 100;
    font-size: 16px;
    color: rgb(239, 239, 241);
    letter-spacing: -0.5px;
}
.row:nth-child(even) {
    /*background-color: #eeeeee;*/
    background-color: #191f2c;
}
.row:first-child {
    border-top: 1px solid #80808085;
}

.row > div {
    padding: 9px 10px 6px;
}

.attribute {
    background: transparent !important;
    color: whitesmoke;
}
.cell {
    border-bottom: 1px solid #80808085;
    border-right: 1px solid #80808085;
    /*background: whitesmoke;*/
    background: inherit;
}
.attribute .cell:first-child {
    border-bottom: 1px solid #80808000;
}
.cell:first-child {
    border-left: 1px solid #80808085;
}
.viewer-console {
    display: flex;
    justify-content: space-between;
    /* padding: 0px 10px; */
    box-sizing: border-box;
    margin-top: 10px;
    color: whitesmoke;
}
.viewer-console .expire-date {
    display: flex;
    padding-left: 1px;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: rgb(255,205,82);
    color: #333;
    padding: 3px 10px 3px 10px;
    box-sizing: border-box;
    border-radius: 2px;
    height: 31px;
    font-family: 'Consolas';
    letter-spacing: -0.5px;
}
.viewer-console .expire-date[data-expire-date="NaN"] {
    background : none;
}

.float-message-box {
    position: absolute;
    right : 30px;
    top : 30px;
    text-align: right;
    width: 100%;
    pointer-events: none;
}
.floating-modal {
    color: rgb(255,205,82);
    background-color: rgb(255,205,82);
    border-radius: 5px;
    text-align: left;
    top: 30px;
    display: flex;
    width: 55px;
    height: 55px;
    box-sizing: border-box;
    margin-left: auto;
    /*max-width: 45%;*/
    right: 30px;
    padding: 15px;
    margin-bottom: 5px;
    /*animation: fadeout 15s;*/
    /*animation-fill-mode: forwards;*/
    transition: 1s;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
    pointer-events: all;
}
.floating-modal:hover {
    color: #333;
    width: 80%;
    transition: 1s;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 90px;
}
@-webkit-keyframes fadeout {
    0% {
        opacity: 1;
    }

    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes fadeout {
    0% {
        opacity: 1;
    }

    70% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.layer-1 {
    position: absolute;
 width: 97%;
 height: 100%;
 border: 0px solid red;
 display: flex;
 box-sizing: border-box;
}
.layer-2 {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background : rgb(255,205,82);
    padding: 5px;
    /* unnecessary styling properties */
    max-width: 50%;
    text-align: center;
    border: 1px solid blue;
}
.--blur >* {
    -webkit-filter: blur(10px);
            filter: blur(10px);
}





.--flex {
    display: flex;
}
.--relative {
    position: relative;
}
.--inner-eye {
    float: right;
    position: absolute;
    color: whitesmoke;
    font-size: 20px;
    margin: 8px 0 0 10px;
}
.--space-between {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    /*border-top: 1px solid whitesmoke;*/
    padding-top: 0px;
    margin-right: 10px;
    margin-top: 2px;
}
.--space-between > .viewer-status {
    flex-grow : 10;
    padding-left: 1px;
    height: 100%;
    justify-content: left;
    align-items: center;
}
.viewer-status > div:nth-child(2) {
    padding-bottom: 5px;
}
.viewer-status .--pointer {
    margin-right: 10px;
}

.viewer-status.--idle .--viewer-progress{
    transition: color 1.2s cubic-bezier(.12,.18,0,.99);
    transform: rotate(0deg);
    color: whitesmoke;
}
.viewer-status.--downloadComplete .--viewer-progress{
    transition: color .3s, transform 2.0s cubic-bezier(.12,.18,0,.99);
    transform: rotate( 360deg );
    color: rgb(255,205,82);
}
.viewer-status.--receiveOldData .--viewer-progress{
    color: rgb(255, 82, 82);
}

.viewer-status .receive-tm {
    -webkit-font-smoothing: subpixel-antialiased;
    -webkit-backface-visibility:visible;
    /* -webkit-transform: translateZ(0px); */
 }

.viewer-status.--idle .receive-tm {
    transition: color 1.2s cubic-bezier(.12,.18,0,.99);;
    color: whitesmoke;
}
.viewer-status.--downloadComplete .receive-tm {
    transition: color .3s;
    color: rgb(255,205,82);
}
.viewer-status.--receiveOldData .receive-tm {
    transition: color .3s;
    color: rgb(255, 82, 82) !important;
}

